import React, { useEffect, useRef, useState } from "react";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import Project from "../../components/Project/Project";
import styles from './home.module.css'
import gsap from 'gsap'
import ScrollTrigger from "gsap/ScrollTrigger";
import useWindowDimensions from '../../hooks/useWindowDimensions'
import About from "../../components/About/About";
import Distort from "../../components/Distort/Distort";
import Contact from "../../components/Contact/Contact";

const Home = props => {
    const {width, height} = useWindowDimensions()

    const project1 = useRef()
    const project2 = useRef()
    const project3 = useRef()

    useEffect(() => {
        const projects = [
            project1.current,
            project2.current,
            project3.current
        ]

        gsap.registerPlugin(ScrollTrigger)

        gsap.to(projects, {
            xPercent: -100 * (projects.length - 1),
            ease: "none",
            scrollTrigger: {
              trigger: ".projects-container",
              start: "8% top",
              pin: true,
              scrub: 1,
              markers: true,
              end: `+=${projects.length > 2? width*(projects.length - 1) : width}`,
            }
          });
    }, [])

    return (
        <div>
            <HomeHeader />
            <div className="projects-container" style={{width: '400vw'}}>
                <Project ref={project1}/>
                <Project ref={project2}/>
                <Project ref={project3}/>
            </div>
            <About projectHeight={width*2}/>
            <Contact projectHeight={width*2}/>
        </div>
    )
}

export default Home